<template>
  <mt-popup
    class="pup"
    v-if="init.key"
    v-model="init.key"
    :modal="false"
    position="bottom">
    <div class="box">
        <div class="b-header" @click.stop="rt()">
            <div>商品图片</div>
            <div>商品名</div>
            <div>销售价</div>
        </div>
        <div class="b-content" ref="wrapper">
           <mt-loadmore :bottom-method="loadBottom" @bottom-status-change="handleBottomChange" :bottom-all-loaded="allLoaded" ref="loadmore">
                <ul class="page-loadmore-list">
                    <li v-if="list.length==0" class="page-loadmore-listitem">
                        <div>
                            暂无数据
                        </div>
                    </li>  
                    <li v-if="list.length>0" :class="defaultList.id==item.id?'active':''" data-id="item.id" v-for="(item,index) in list" :key="index" class="page-loadmore-listitem" @click="checkFn(item)">
                        <div>
                            <img :src="item.thumbnailSrc" alt="">
                        </div>
                        <div>
                           {{ item.name }}
                        </div>
                        <div>
                            {{item.price}}
                        </div>
                    </li>
                </ul>
                <div slot="bottom" class="mint-loadmore-bottom">
                <span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">↑</span>
                <span v-show="bottomStatus === 'loading'" class="loading-icon">
                    <mt-spinner type="snake"></mt-spinner>
                </span>
                </div>
            </mt-loadmore>
        </div>
        <div class="b-footer">
            <button @click.stop="close">取消</button>
            <button @click.stop="update($event)">确定</button>
        </div>
    </div>
  </mt-popup>
</template>
<script>
import Exotic from '../../config.js';
import _$ from 'expose-loader?underscore!underscore';
// import { MessageBox } from 'mint-ui';
    export default{
        created(){
            let that=this;
            var param = {
                token:that.token,
                tradeAreaId: that.taid,
                isOnLine: 1,
                pageSize: 10,
                pageNumber: 1,
            };
            Exotic.ajaxData(that.token,Exotic.urlMap.product.list, param, 'get', function(res) {
                _$.each(res.data, function(item,i) {
                    item.thumbnailSrc = Exotic.imageUrl + item.thumbnail;
                    that.allGoods.push(item);
                });
                that.list=res.data;
                console.log(res);
                that.pageNumber = res.pageNumber;
                that.totalPages = res.totalPages;
            });
        },
        data(){
            return {
                //列表
                list: [
                    
                ],
                //选择的数据
                defaultList:{
                    id:null,
                    name:'',
                    price:null,
                    logo:''
                },
                allLoaded: false,//全部加加载完毕
                bottomStatus: '',
                pageNumber:1,//当前页数
                totalPages:1,//总页数
                allGoods:[]
            }
        },
        props:['init','type','token','id','taid'],
        methods:{
            //点击选中高亮
            handleBottomChange(status) {
                this.bottomStatus = status;
            },
            //下拉加载
            loadBottom() {
                let that=this;
                setTimeout(() => {
                   if(this.pageNumber < this.totalPages) {
                        this.pageNumber++;
                        var param = {
                            tradeAreaId: this.taid,
                            isOnLine: 1,
                            pageSize: 10,
                            pageNumber: this.pageNumber,
                        };
                        //请求数据
                        Exotic.ajaxData(that.token,Exotic.urlMap.product.list, param, 'get',function(res) {
                            //添加数据到列表，合并图片路径
                            _$.each(res.data, function(item,i) {
                                item.thumbnailSrc = Exotic.imgUrl + item.thumbnail;
                                that.list.push(item);
                            });
                            that.pageNumber = res.pageNumber;
                            that.totalPages = res.totalPages;
                        });
                    }else {
                        this.allLoaded = true;
                    }
                    try{
                        this.$refs.loadmore.onBottomLoaded();
                    }catch(err){

                    }
                }, 1500);
            },
            //列表点击事件
            checkFn(item){
                this.defaultList.id=item.id;
                this.defaultList.name=item.name;
                this.defaultList.logo=item.thumbnailSrc;
                this.defaultList.thumbnail=item.thumbnail;
                this.defaultList.price=item.price;
            },
            //确定商品
            update(){
                if(this.defaultList.id!=null){
                    this.$emit('setlist',this.defaultList);
                    this.init.key=false;
                }else{
                    this.MintUI.MessageBox('提示', '请选择商品');
                }
            },
            rt(){
                return true;
            },
            close(){
                this.init.key=false;
            }
        },
        mounted() {
    
        }
    }
</script>
<style scoped lang="scss">
.box {
    width: 86%;
    position: absolute;
    z-index: 66;
    top: 50%;
    left: 7%;
    height: 400px;
    border-radius: 3px;
    background: #fff;
    margin-top:-200px;
  }
  .box .b-header {
    height: 40px;
    line-height: 42px;
    font-size: 14px;
    border-bottom: 1px solid #eee;
  }
  .box .b-header div {
    float:left;
    width:25%;
  }
  .box .b-header div:nth-child(1) {
    width:25%;
  }
  .box .b-header div:nth-child(2) {
    width:50%;
  }
  .box .b-header div:nth-child(3) {
    width:25%;
  }
  .box .b-content {
    height:313px;
    width: 100%;
    overflow-y: auto;
  }
  .mint-loadmore{
    text-align: center;
  }
  .box .b-footer {
    height: 45px;
    line-height: 45px;
    overflow: hidden;
    border-top: 1px solid #eee;
  }
  .box .b-footer button {
    width:50%;
    text-align: center;
    background: transparent;
    border: 0;
    padding: 0;
    line-height: 45px;
    margin: 0;
    font-size: 14px;
    outline: none;
    float:left;
  }
  .box .b-footer button:active {
    background: #ddd;
  }
  .box .b-footer button:first-child {
    border-radius: 0 0 0 3px;
    border-right: 1px solid #eee;
  }
  .box .b-footer button:last-child {
    color: #e56fa6;
    border-radius: 0 0 3px 0;
  }
  .page-loadmore-list li {
    height: 50px;
    line-height: 52px;
    border-bottom: 1px solid #eee;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .page-loadmore-list li div {
    overflow: hidden;
    float: left;
    font-size:14px;
  }
  .page-loadmore-list li div img {
    width: 44px;
    height: 44px;
    display: block;
    margin:0 auto;
    margin-top:3px;

  }
  .page-loadmore-list li div:nth-child(1) {
    width:25%;
  }
  .page-loadmore-list li div:nth-child(2) {
    width:50%;
  }
  .page-loadmore-list li div:nth-child(3) {
    width:25%;
  }
  .page-loadmore-list .active {
    border: #e56fa6 1px solid;
    background-color: #fff0f7;
  }
  .loading-icon {
    display: inline-block;
  }
  .check i {
    width: 16px;
    height: 16px;
    display: block;
    border: 1px solid #ccc;
  }
</style>



